#updateData{
    position:fixed;
    width: 100%;
    height: 50px;
    top: 55px;
    border-bottom: 1px solid #ccc;
    background: white;
    display:flex;
    align-items: center;
    z-index: 0;
}
#update_data_container{
    width: 90%;
    display:flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}
#search{
    width: 200px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}
#search:focus{
    outline: none;
}
#execute{
    font-size: 14px;
    width: 100px;
    height: 30px;
    border: 1px solid #5f5fb3;
    border-radius: 3px;
    background: #474787;
    color: white;
    margin-left: 10px;
}

#execute:hover{
    cursor: pointer;
    background: #5f5fb3;
}
#addDataBtn{
    font-size: 14px;
    width: 100px;
    height: 30px;
    border: 1px solid #34ace0;
    border-radius: 3px;
    background: #227093;
    color: white;
    margin-left: 10px;
}
#addDataBtn:hover{
    cursor: pointer;
    background: #34ace0;
}