#nodemap_wrapper{
    width: 100%;
    height: 55px;
    border-bottom: 1px solid #ccc;
    background: #2c2c54;
    position: fixed;
    z-index: 1;
    color: white;
}

#nodemap_container{
    width: 90%;
    display: flex;
    align-items: center;
    margin: 0 auto;
    justify-content: space-between;
}

#nodemap_container_left{
    display:flex;
}

#logo{
    font-size: 20px;
    padding: 3px;
    background: #141426;
}

#nodemap{
    display: flex;
    align-self: center;
}
#nodemap h1{
    font-size: 16px;
    margin-left: 10px;
    padding: 3px;
    border-radius: 3px;
}

.node_down{
    background: red;
}
.node_down:hover{
    cursor: pointer;
}

#add_node{
    border: 1px solid #5f5fb3;
    background: #474787;
}

#add_node:hover{
    cursor: pointer;
    background: #5f5fb3;
}